<template>
	<div  class="Receipt">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>录入单号</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div class="import">
				<span>收货单号</span>
				<input  placeholder="请输入内容" @keyup.enter="ToTakeDelivery"   ref="ReceiptNum"  v-model="ReceiptNum" ></input>
				<i v-if="ReceiptNum" @click="clearInput('ReceiptNum')" class="iconfont icon-cuowu"></i>
				<i class="iconfont icon-sousuo"  @click="toSelectOrder" ></i>
			</div>
			<div  class="import">
				<span>客户单号</span>
				<input  placeholder="请输入内容"  ref="CustomerNum"  @keyup.enter="ToTakeDelivery"   v-model="CustomerNum"></input>
				<i v-if="CustomerNum" @click="clearInput('CustomerNum')"  class="iconfont icon-cuowu"></i>
			</div>
			<div class="g_doubleBtn">
				<div @click="ToTakeDelivery">确定</div>
				<div @click="ToSummary">汇总查看</div>
			</div>
		</div>
		<div v-if="tipsHide" @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @ref="inputFocus" @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"Receipt",
		data(){
			return{
				ReceiptNum:'',//0000002948
				CustomerNum:'',
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",
			}
		},
		mounted() {
			this.$refs['ReceiptNum'].focus();
		},
		created(){

			var that = this;

			// 在输入框填写 从其他页面传来的收货单号 和 客户单号 
			if(this.$route.query.ReceiptKey){

				that.ReceiptNum = that.$route.query.ReceiptKey;
				that.CustomerNum = that.$route.query.ExternalReceiptKey2;
			}
		},
		methods:{
			//返回上一页 首页
			toIndex:function(){
				this.$router.push({
					name:"Index",
				})
			},
			//跳转到收货页面
			ToTakeDelivery:function(){

				var that = this;

				if(!(this.ReceiptNum || this.CustomerNum )){
					
					that.msg = "请至少填写一个信息";

					that.inputFocus = "ReceiptNum";

					that.$refs["CustomerNum"].blur();

					that.tipsShow("ReceiptNum");

					return;
				}

				var ReceiptUrl = this.HOST + "RFReceiptNo.do";

				var r_postData = {
					ReceiptKey : this.ReceiptNum,
					ExternalReceiptKey2 : this.CustomerNum
				}

				this.httpRequest(ReceiptUrl,r_postData,function(res){

					if(res.data.data.length){

						that.$store.state.Receipt_SubmitData = res.data.data;

						that.$router.push({
							name:"TakeDelivery"
						})

					}else{

						that.tipsShow("ReceiptNum");

						that.inputFocus = "ReceiptNum";

						that.msg = "未查询到数据";
					}
				},"ReceiptNum")
			},
			//跳转到收货汇总页面
			ToSummary:function(){

				var that = this;

				if(!(this.ReceiptNum || this.CustomerNum)){

					that.tipsShow("ReceiptNum");

					that.inputFocus = "ReceiptNum";

					that.msg = "请至少填写一个信息";

					return;
				}

				var SummaryUrl = this.HOST + "RFReceiptSum.do";

				var sum_Data = {
					ReceiptKey : this.ReceiptNum,
					ExternalReceiptKey2 : this.CustomerNum
				}

				this.httpRequest(SummaryUrl,sum_Data,function(res){

					if(res.data.data.length){

						that.$store.state.Receipt_SummaryData = res.data.data;

						that.$router.push({
							name:"Summary",
							query:{
								title:"收货汇总",
								ifSelect:false,
								Receipt:{
									ReceiptKey: that.ReceiptNum,
									ExternalReceiptKey2: that.CustomerNum
								}
							}
						})
					}else{

						that.tipsShow("ReceiptNum");
						
						that.msg = "未查询到数据";

						that.inputFocus = "ReceiptNum";
					}
				},"ReceiptNum")
			},
			// 跳转到 选择订单 页面
			toSelectOrder:function(){

				var that = this;

				var currentUrl = this.HOST + "RFReceiptSel.do";

				var postData = {
					ReceiptKey : this.ReceiptNum,
					ExternalReceiptKey2 : this.CustomerNum
				}

				this.httpRequest(currentUrl,postData,function(res){

					if(res.data.data.length){

						that.$store.state.Receipt_SelectOrderData = res.data.data;

						that.$router.push({
							name:"SelectOrder",
							query:{
								ReceiptKey: that.ReceiptNum,
								ExternalReceiptKey2: that.CustomerNum
							}
						})
					}else{

						that.tipsShow("ReceiptNum");

						that.msg = "未查询到数据";

						that.inputFocus = "ReceiptNum";
					}
				},"ReceiptNum")
			}
		}
	}
</script>
<style scoped lang="less">
.Receipt{
	&>.container{
		font-size: 30/50rem;
		input::placeholder{
			font-size: 25/50rem;
		}
		input{
			font-size: 25/50rem;
		}
		&>div.import{
			width:100%;
			height: 80/50rem;
			padding: 25/50rem 20/50rem 0;
			box-sizing: border-box;
			position: relative;
			span{
				
				width: 140/50rem;
				text-align: right;
			}

			input{
				margin-left: 10/50rem;
				width:490/50rem;	
				height:55/50rem;
				padding:0 10/50rem;
			}
			i{
				position:absolute;
				right:90/50rem;
				font-size: 30/50rem;
				top: 36/50rem;
			}
			i.icon-sousuo{
				right:30/50rem;
				font-size: 40/50rem;
				top: 30/50rem;
			}
			
		}
	}
}
</style>


