<template>
	<div class="SelectOrder">
		<header><i class="iconfont icon-xiangzuo1" @click="toReceipt"></i><span>选择订单</span></header>
		<div class="container">
			<div>
				<span>选择</span>
				<span>收货单号</span>
				<span>货主</span>
				<span>客户单号</span>
				<span>总数量</span>
				<span>已收数量</span>
				<span>sku品类</span>
			</div>
			<div v-for='(dataItem,index) in Receipt_SelectOrderData'  :id="index"  :key='index'  @click="selectData(index)">
				<span class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></span>
				<span>{{dataItem.receiptKey}}</span>
				<span>{{dataItem.storerKey}}</span>
				<span>{{dataItem.externalreceiptkey2}}</span>
				<span>{{dataItem.qtytotal}}</span>
				<span>{{dataItem.qtyreceived}}</span>
				<span>{{dataItem.skucnt}}</span>
			</div>
			<div class="kong"></div>
			<p class="positionFixed"><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"SelectOrder",
		data(){
			return{

				Receipt_SelectOrderData:[],//列表数据
				detail_item:0,//默认为第一个

			}
		},
		created(){

			console.log(this.$store.state.Receipt_SelectOrderData)

			var Receipt_SelectOrderData = this.$store.state.Receipt_SelectOrderData;

			this.Receipt_SelectOrderData = Receipt_SelectOrderData;
		},
		methods:{
			// 返回上一页 录入单号 页面
			toReceipt:function(){

				var that = this;

				this.$router.push({
					name:"Receipt",
					query:{
						ReceiptKey:that.$route.query.ReceiptKey,
						ExternalReceiptKey2:that.$route.query.ExternalReceiptKey2
					}
				})
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;

				
			},
			// 点击确认按钮 跳转到录入单号页面
			selectData_submit:function(){


				var receiptKey = this.Receipt_SelectOrderData[this.detail_item].receiptKey//收货单号
				var externalreceiptkey2 =  this.Receipt_SelectOrderData[this.detail_item].externalreceiptkey2//客户单号

				this.$router.push({
					name:"Receipt",
					query:{
						ReceiptKey:receiptKey,
						ExternalReceiptKey2:externalreceiptkey2
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.SelectOrder{
		&>.container{
			width:100%;
			font-size: 30/50rem;
			padding-bottom: 130rpx;
			&>div{
				overflow: hidden;
				width:1300/50rem;
				height: 80/50rem;
				line-height: 80/50rem;
				border-bottom: 1/50rem solid #999;
				text-align: center;
				&>span{
					float: left;
					height:100%;
					width:200/50rem;
					text-align:center;
				}
				&>span:nth-child(1),&>span:nth-child(7){
					width:140/50rem;
				}
				.iconfont{
					font-size: 40/50rem;
				}
			}
			&>div.kong{
				height:200/50rem;
				border-bottom: none;
			}
			&>p.positionFixed{
				width: 100%;
				height:130/50rem;
				position: fixed;
				bottom: 0;

			}
		}
	}
</style>


