import Vue from 'vue'
import Router from 'vue-router'

// 登录
import Login from "@/pages/Login/login" 

//首页
import Index from '@/pages/index'

//库存查询
import InventoryQuery from '@/pages/InventoryQuery/InventoryQuery'    //库存查询
import InventoryDetails from '@/pages/InventoryQuery/InventoryDetails'   //库存详情
import InventoryList from '@/pages/InventoryQuery/InventoryList'   //库存列表

//上架
import Putaway from '@/pages/Putaway/Putaway'
import PutawayDetails from '@/pages/Putaway/PutawayDetails'

//收货
import Receipt from "@/pages/Receipt/Receipt"   //收货首页（录入单号）
import TakeDelivery from "@/pages/Receipt/TakeDelivery"   // 收货
import Summary from "@/pages/Receipt/Summary"     //收货汇总
import SelectOrder from "@/pages/Receipt/SelectOrder"   //选择订单

//盘点
import CycleCount from '@/pages/CycleCount/CycleCount'
import CycleCount_ChoiceNo from '@/pages/CycleCount/CycleCount_ChoiceNo'
import CycleCount_Submit from '@/pages/CycleCount/CycleCount_Submit'

// 拣货
import RFPick from '@/pages/RFPick/RFPick'  //拣货首页
import RFPick_select from "@/pages/RFPick/RFPick_select"  //拣货 选择任务
import RFPick_confirm from "@/pages/RFPick/RFPick_confirm"  //拣货 拣货确认页面
import Scan from "@/pages/RFPick/scan"

// 库存移动
import RFInvMove from '@/pages/RFInvMove/RFInvMove'  //库存移动首页

// 发货序列号采集
import ConsignmentIndex from "@/pages/ConsignmentNumCollect/ConsignmentIndex"    //发货序列号采集 首页
import ConsignmentNumCollect from "@/pages/ConsignmentNumCollect/ConsignmentNumCollect"   //发货序列号采集
import CheckDetails from "@/pages/ConsignmentNumCollect/checkDetails"   //发货序列号采集  查看明细
import ChoiceGoods from "@/pages/ConsignmentNumCollect/ChoiceGoods"   //发货序列号采集  选择商品

// 发运
import RFOrderShip from "@/pages/RFOrderShip/RFOrderShip" //发运首页
import RFOrderDetailQuery from "@/pages/RFOrderShip/RFOrderDetailQuery"  // 查看发运明细


Vue.use(Router)

export default new Router({
  routes: [
    // 登录
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    // 首页
    {
      path: '/Index',
      name: 'Index',
      component: Index
    },
    // 库存查询
    {
      path: '/InventoryQuery',
      name: 'InventoryQuery',
      component: InventoryQuery
    },
    {
      path:'/InventoryDetails',
      component: InventoryDetails,
      name: 'InventoryDetails',
    },
    {
      path:'/InventoryList',
      component: InventoryList,
      name: 'InventoryList',
    },
    // 上架
    {
      path: '/Putaway',
      name: 'Putaway',
      component: Putaway
    },
    {
      path:'/PutawayDetails',
      component: PutawayDetails,
      name: 'PutawayDetails',
    },
    // 收货
    {
      path:'/Receipt',
      component: Receipt,
      name: 'Receipt',
    },
    {
      path:'/TakeDelivery',
      component: TakeDelivery,
      name: 'TakeDelivery',
    },
    {
      path:'/Summary',
      component: Summary,
      name: 'Summary',
    },
    {
      path:'/SelectOrder',
      component: SelectOrder,
      name: 'SelectOrder',
    },
    // 盘点
    {
      path:'/CycleCount',
      component: CycleCount,
      name: 'CycleCount',
    },
    {
      path:'/CycleCount_ChoiceNo',
      component: CycleCount_ChoiceNo,
      name: 'CycleCount_ChoiceNo',
    },
    {
      path:'/CycleCount_Submit',
      component: CycleCount_Submit,
      name: 'CycleCount_Submit',
    },
    //拣货
    {
      path:'/RFPick',
      component: RFPick,
      name: 'RFPick',
    },
    {
      path:"/RFPick_select",
      component: RFPick_select,
      name: 'RFPick_select',
    },
    {
      path:"/RFPick_confirm",
      component: RFPick_confirm,
      name: 'RFPick_confirm'
    },
    {
      path:"/Scan",
      component: Scan,
      name: 'Scan'
    },
    //库存移动
    {
      path:'/RFInvMove',
      component: RFInvMove,
      name: 'RFInvMove',
    },
    // 发货
    {
      path:'/ConsignmentIndex',
      component: ConsignmentIndex,
      name: 'ConsignmentIndex',
    },
    {
      path:'/ConsignmentNumCollect',
      component: ConsignmentNumCollect,
      name: 'ConsignmentNumCollect',
    },
    {
      path:'/CheckDetails',
      component: CheckDetails,
      name: 'CheckDetails',
    },
    {
      path:'/ChoiceGoods',
      component: ChoiceGoods,
      name: 'ChoiceGoods',
    },
    // 发运
    {
      path:'/RFOrderShip',
      component: RFOrderShip,
      name: 'RFOrderShip',
    },
    {
      path:'/RFOrderDetailQuery',
      component: RFOrderDetailQuery,
      name: 'RFOrderDetailQuery',
    }
  ]
})



