<template>
	<div class="Summary">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span>{{title}}</span></header>
		<div class="container">
			<div>
				<span v-if="ifSelect">选择</span>
				<span class="goodscode">商品代码</span>
				<span>待收数量</span>
				<span>预期数量</span>
				<span>已收数量</span>
				<p>商品名称</p>
				<span>单位</span>
			</div>
			<div v-for='(dataItem,index) in Receipt_SummaryData'  :id="index"  :key='index'   @click="selectData(index)">
				<span v-if="ifSelect" class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></span>
				<span class="goodscode">{{dataItem.sku}}</span>
				<span>{{dataItem.qtyremain}}</span>
				<span>{{dataItem.qtyexpected}}</span>
				<span>{{dataItem.qtyreceived}}</span>
				<p>{{dataItem.descr}}</p>
				<span>{{dataItem.susr2}}</span>
			</div>
			<div class="kong"></div>
			<p class="positionFixed"  v-if="ifSelect"><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"Summary",
		data(){
			return{
				Receipt_SummaryData:[],//列表数据
				title:"收货汇总",
				detail_item:0,//默认为第一个(选择商品)
				ifSelect:false,//是否显示选择项
			}
		},
		created:function(){

			this.title = this.$route.query.title;

			this.ifSelect = this.$route.query.ifSelect;


			if(this.$store.state.Receipt_SummaryData){

				var Receipt_SummaryData = this.$store.state.Receipt_SummaryData;

				this.Receipt_SummaryData = Receipt_SummaryData;
			}
		},
		methods:{
			//返回上一页
			goPrev:function(){

				var that = this;

				if(this.$route.query.Receipt){
					this.$router.push({
						name:"Receipt",
						query:{
							ReceiptKey:that.$route.query.Receipt.ReceiptKey,
							ExternalReceiptKey2: that.$route.query.Receipt.ExternalReceiptKey2
						}
					})
				}else if(this.$route.query.TakeDelivery){
					this.$router.push({
						name:"TakeDelivery",
						query:{
							ExternalReceiptKey2: that.$route.query.TakeDelivery.ExternalReceiptKey2
						}
					})
				}

			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;
			},
			// 点击确认按钮 跳转到 收货页面
			selectData_submit:function(){

				var that = this;

				this.$router.push({
					name:"TakeDelivery",
					query:{
						Summary:{
							descr:that.Receipt_SummaryData[that.detail_item].descr,//商品名称
							qtyreceived:that.Receipt_SummaryData[that.detail_item].qtyreceived,//已收数量
							qtyremain:that.Receipt_SummaryData[that.detail_item].qtyremain,//待收数量
							sku:that.Receipt_SummaryData[that.detail_item].sku,//商品代码
							susr2:that.Receipt_SummaryData[that.detail_item].susr2,//单位
						}
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.Summary{
		&>.container{
			width:100%;
			font-size: 30/50rem;
			&>div{
				overflow: hidden;
				width:1550/50rem;
				height: 80/50rem;
				line-height: 80/50rem;
				border-bottom: 1/50rem solid #999;
				text-align: center;
				&>span{
					float: left;
					height:100%;
					width:185/50rem;
					text-align:center;
				}
				&>p{
					float:left;
					width: 400/50rem;
				}
				&>span.goodscode{
					width:225/50rem;
				}
				&>span:last-child{
					width:130/50rem;
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}
			&>div.kong{
				height:200/50rem;
				border-bottom: none;
			}
			&>p.positionFixed{
				width: 100%;
				height:130/50rem;
				position: fixed;
				bottom: 0;

			}
		}
	}
</style>


