<template>
	<div class="InventoryList">
		<header><i class="iconfont icon-xiangzuo1" @click="sendParams"></i><span>库存列表</span></header>
		<div class="container">
			<div>
				<span>商品代码</span>
				<span>数量</span>
				<span>库位</span>
			</div>
			<div v-for='(dataItem,index) in dataList'  :id="index"  :class='{selectDiv:(index+1)==nowData}'  :key='index'  @click="selectData(index)">
				<div>
					<span>{{dataItem.sku}}</span>
					<span>{{dataItem.qty}}</span>
					<span>{{dataItem.loc}}</span>
				</div>
				<div><i class="iconfont icon-xiangyou1"></i></div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"InventoryList",
		data(){
			return{
				nowData:0,//具体为第几条数据
				dataList:[],//数据列表
			}
		},
		created(){
			console.log(this.$route.query.nowData)

			this.nowData = this.$route.query.nowData;

			var dataList = this.global.QueryListData;

			this.dataList = dataList;
		},
		methods:{
			// 返回上一页 传参
			sendParams:function(){

				var that = this;

				this.$router.push({

					name:"InventoryDetails",
					query:{
						nowData:that.nowData
					}
				})
			},
			//选中具体数据
			selectData:function(index){

				this.nowData = index+1;

				this.$router.push({

					name:"InventoryDetails",
					query:{
						nowData:index+1
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.InventoryList{
		&>.container{
			font-size: 30/50rem;
			&>div{
				overflow: hidden;
				width:100%;
				height: 80/50rem;
				line-height: 80/50rem;
				border-bottom: 1/50rem solid #999;
				text-align: center;
				&>div{
					float: left;
					&>span{
						float:left;
					}
					&>span:nth-child(1){
						width:250/50rem;
					}
					&>span:nth-child(2){
						width:150/50rem;

					}
					&>span:nth-child(3){
						width:230/50rem;

					}
				}
				&>div:nth-child(1){
					width:630/50rem;
					margin-left: 20/50rem;
				}
				&>div:nth-child(2){
					color: #285DCE;
					width:50/50rem;
				}
			}
			&>div:nth-child(1){
				&>span{
					float:left;
				}
				&>span:nth-child(1){
					width:250/50rem;
					margin-left: 20/50rem;
				}
				&>span:nth-child(2){
					width:150/50rem;
				}
				&>span:nth-child(3){
					width:230/50rem;
				}
			}
			&>div.selectDiv{
				background-color:rgba(0,0,0,0.08);
			}
		}
	}
</style>


