<template>
	<div class="RFPick_select">
		<header><i class="iconfont icon-xiangzuo1" @click="toReceipt"></i><span>选择订单</span></header>
		<div class="container">
			<div>
				<span>选择</span>
				<span>任务组号</span>
				<span>拣货数量</span>
				<span>任务数</span>
				<span>库位数</span>
				<span>商品品类数</span>
				<span>任务下发时间</span>
			</div>
			<div v-for='(dataItem,index) in RFPick_SelectOrderData'  :id="index"  :key='index'  @click="selectData(index)">
				<span class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></span>
				<span>{{dataItem.TaskGroup}}</span>
				<span>{{dataItem.Qty}}</span>
				<span>{{dataItem.TaskCnt}}</span>
				<span>{{dataItem.LocCnt}}</span>
				<span>{{dataItem.SkuCnt}}</span>
				<span>{{dataItem.AddDate}}</span>
			</div>
			<div class="kong"></div>
			<p class="positionFixed" ><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"RFPick_select",
		data(){
			return{

				RFPick_SelectOrderData:[],//列表数据
				detail_item:0,//默认为第一个

			}
		},
		created(){
			console.log(this.$store.state.RFPick_SelectOrderData)

			if(this.$store.state.RFPick_SelectOrderData.length){

				var RFPick_SelectOrderData = this.$store.state.RFPick_SelectOrderData;

				this.RFPick_SelectOrderData = RFPick_SelectOrderData;

			}
		},
		methods:{
			// 返回上一页 录入单号 页面
			toReceipt:function(){

				var that = this;

				this.$router.push({
					name:"RFPick",
					query:{
						TaskGroup:that.$route.query.TaskGroup
					}
				})
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;

				
			},
			// 点击确认按钮 跳转到录入单号页面
			selectData_submit:function(){


				var TaskGroup = this.RFPick_SelectOrderData[this.detail_item].TaskGroup//任务组号

				this.$router.push({
					name:"RFPick",
					query:{
						TaskGroup:TaskGroup
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.RFPick_select{
		&>.container{
			width:100%;
			font-size: 30/50rem;
			&>div{
				overflow: hidden;
				width:1450/50rem;
				height: 80/50rem;
				line-height: 80/50rem;
				border-bottom: 1/50rem solid #999;
				text-align: center;
				&>span{
					float: left;
					height:100%;
					width:219/50rem;
					text-align:center;
				}
				&>span:nth-child(1){
					width:130/50rem;
				}
				.iconfont{
					font-size: 40/50rem;
				}
				.icon-xuanzhong{
					color:#285DCE;
				}
			}
			&>div.kong{
				height:200/50rem;
				border-bottom: none;
			}
			&>p.positionFixed{
				width: 100%;
				height:130/50rem;
				position: fixed;
				bottom: 0;

			}
		}
	}
</style>


