<template>
	<div class="InventoryDetails">
		<header><i class="iconfont icon-xiangzuo1" @click="sendParams"></i><span>库存详情</span></header>
		<div class="container">
			<div  class="Pallet">
				<div>
					<span>库位代码</span>
					<input   readonly  v-model="LibraryCode"></input>
				</div>
				<div>
					<span>托盘号</span>
					<input   readonly v-model="PalletNum" ></input>
				</div>
			</div>
			<div>
				<div>
					<span>商品代码</span>
					<input   readonly  v-model="CommodityCode"></input>
				</div>
				<div>
					<span>客户代码</span>
					<input   readonly  v-model="CustomerCode"></input>
				</div>
			</div>

			<div class="goodsName">
				<span>商品名称</span>
				<textarea v-model="descr" readonly></textarea>
			</div>
			<div class="property"> 
				<span>批次属性</span>
				<textarea readonly>
货物状态:{{lottable02}}
生产批号:{{lottable03}}
生产日期:{{lottable04}}
到期日期:{{lottable05}}
收货日期:{{lottable09}}
				</textarea>
			</div>
			<div>
				<div>
					<span>系统数量</span>
					<input   readonly  v-model="SystemQty"></input>
				</div>
				<div>
					<span>单位</span>
					<input    readonly  v-model="unit"></input>
				</div>
			</div>
			<div>
				<div>
					<span>可用数量</span>
					<input   readonly  v-model="AvailableQty"></input>
				</div>
				<div>
					<span>拣配数量</span>
					<input   readonly  v-model="SortingQty"></input>
				</div>
			</div>
			<div>
				<div>
					<span>系统批次</span>
					<input   readonly  v-model="SystemBatch"></input>
				</div>
				<div>
					<span>库存状态</span>
					<input    readonly v-model="InventoryState"></input>
				</div>
				
			</div>
			<div class="g_switchTab">
				<div @click="previous" class="previous"><i class="iconfont icon-xiangzuo1"></i></div>
				<span>{{nowData}}/{{totalData}}</span>
				<div @click="next" class="next"><i class="iconfont icon-xiangyou1"></i></div>
			</div>
			<span class="g_singleBtn" @click='toInventoryList'>列表查看</span>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"InventoryDetails",
		data(){
			return{
				LibraryCode:'',
				PalletNum:'',
				CommodityCode:'',
				CustomerCode:'',
				SystemQty:'',
				unit:'',
				SystemBatch:'',//系统批次0000000408
				InventoryState:'',
				AvailableQty:'',
				SortingQty:'',
				descr:'',
				lottable02:'',
				lottable03:'',
				lottable04:'',
				lottable05:'',
				lottable09:'',
				dataList:[],//数据列表
				totalData:0,//一共几条数据
				nowData:1,//当前查看的是第几条数据
			}
		},
		created(){

			var that = this;

			var dataList = this.global.QueryListData;

			this.dataList = dataList;

			this.totalData = dataList.length;

			//判断是否是从列表页面返回的
			if(this.$route.query.nowData){

				this.nowData = this.$route.query.nowData;

			}else{

				this.nowData = 1;
			}

			this.SwitchingData();

		},
		methods:{
			//切换到上一条数据
			previous:function(){

				if(this.nowData<=1){
					return;
				}
				this.nowData --;

				this.SwitchingData();

			},
			//切换到下一条数据
			next:function(){

				if(this.nowData>=this.totalData){
					return;
				}

				this.nowData ++;

				this.SwitchingData();

			},
			SwitchingData:function(){

				var dataList = this.dataList;

				var idx = this.nowData - 1;
 
				this.LibraryCode = dataList[idx].loc;

				this.PalletNum = dataList[idx].pid;

				this.CommodityCode = dataList[idx].sku;

				this.CustomerCode = dataList[idx].storerkey;

				this.descr = dataList[idx].descr;

				this.lottable02 = dataList[idx].lottable02;

				this.lottable03 = dataList[idx].lottable03;

				this.lottable04 = dataList[idx].lottable04;

				this.lottable05 = dataList[idx].lottable05;

				this.lottable09 = dataList[idx].lottable09;

				this.SystemQty = dataList[idx].qty;

				this.unit = dataList[idx].susr2;

				this.SystemBatch = dataList[idx].lot;

				this.InventoryState = dataList[idx].status;

				this.AvailableQty = dataList[idx].qtyavailable;

				this.SortingQty = dataList[idx].qtyallocpick;
			},
			toInventoryList:function(){

				var that = this;

				//跳转到库存列表页面
				this.$router.push({

					name:"InventoryList",
					query:{
						nowData:that.nowData
					}
				})
			},
			sendParams:function(){

				var that = this;

				if(that.global.InventoryQuery){

					//返回到上一页（库存查询页面）
					this.$router.push({
	
						name:"InventoryQuery",
						query:{
						}
					})
				}else{

					//返回到上一页（库存移动页面）
					this.$router.push({
						name:"RFInvMove",
						query:{
						}
					})
					
				}
			}
		}
	}
</script>
<style scoped lang="less">
.InventoryDetails{
	.container{
		font-size: 29/50rem;
		padding-top: 10/50rem;
		input{
			font-size: 25/50rem;
		}
		&>div{
			width:100%;
			height: 50/50rem;
			margin-bottom: 20/50rem;
			box-sizing: border-box;
			overflow: hidden;
			&>div{
				width:365/50rem;
				float: left;
				span{
					width: 130/50rem;
					display:inline-block;
					text-align: right;
				}
				input{
					width:210/50rem;	
					height:50/50rem;
					padding:0 10/50rem;
					box-sizing: border-box;
				}
			}
		}
		&>.goodsName,&>.property{
			height:70/50rem;
			margin-bottom: 15/50rem;
			&>span{
				width: 130/50rem;
				text-align: right;
				float: left;
			}
			&>textarea{
				width: 570/50rem;
				height:70/50rem;
				font-size: 25/50rem;
				padding:0 10/50rem;
				box-sizing: border-box;
				float:left;
				outline:none;
				margin-left: 10/50rem;
			}
		}
		&>.property{
			height:170/50rem;
			&>textarea{
				height:170/50rem;
			}
		}	
		.total{
			text-align: center;
		}
	}
}

@media screen and (max-width: 329px) and (min-width: 311px){
	.InventoryDetails{
		.container{
			&>div{
				margin-bottom: 0;
			}
		}
	}
}
@media screen and (max-width: 310px){
	.InventoryDetails{
		.container{
			&>div{
				margin-bottom: 0;
				&>div{
					width:395/50rem;
					span{
						width:150/50rem;
					}
					input{
						width:230/50rem;
					}
				}
				&>div:nth-child(2){
					width:355/50rem;
					input{
						width:190/50rem;
					}
				}
			}
			 &>div.Pallet{
				&>div{
					width:340/50rem;
					input{
						width:170/50rem;
					}
				}
				&>div:nth-child(2){
					width:395/50rem;
					span{
						width:115/50rem;
					}
					input{
						width:265/50rem;
					}
				}
			}
			&>.goodsName,&>.property{
				height: 80/50rem;
				&>span{
					width: 150/50rem;
					text-align: right;
				}
				&>textarea{
					height: 80/50rem;
				}
			}
			&>.property{
				height:220/50rem;
				&>textarea{
					height:220/50rem;
				}
			}
		}
	}
}
</style>


