<template>
	<div class="CheckDetails">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span>查看明细</span></header>
		<div class="container">
			<div>
				<span>序列号</span>
				<span>商品代码</span>
				<span>箱号</span>
				<span>商品名称</span>
			</div>
			<div v-for='(dataItem,index) in Consignment_CheckDetailsData'  :id="index"  :key='index'   @click="selectData(index)">
				<span>{{dataItem.SerailNo}}</span>
				<span>{{dataItem.Sku}}</span>
				<span>{{dataItem.CaseNo}}</span>
				<span>{{dataItem.Descr}}</span>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"CheckDetails",
		data(){
			return{
				Consignment_CheckDetailsData:[],//列表数据
				title:"收货汇总",
				detail_item:0,//默认为第一个(选择商品)
				ifSelect:false,//是否显示选择项
			}
		},
		created:function(){

			this.title = this.$route.query.title;

			this.ifSelect = this.$route.query.ifSelect;


			if(this.$store.state.Consignment_CheckDetailsData){

				var Consignment_CheckDetailsData = this.$store.state.Consignment_CheckDetailsData;

				this.Consignment_CheckDetailsData = Consignment_CheckDetailsData;
			}
		},
		methods:{
			//返回上一页
			goPrev:function(){

				var that = this;

				this.$router.push({
					name:"ConsignmentIndex",
					query:{
						InvoiceNum:that.$route.query.InvoiceNum,//发货单号
						CommodityCode:that.$route.query.CommodityCode,//商品代码
					}
				})
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;
			},
			// 点击确认按钮 跳转到 收货页面
			selectData_submit:function(){

				var that = this;

				this.$router.push({
					name:"TakeDelivery",
					query:{
						Summary:{
							descr:that.Consignment_CheckDetailsData[that.detail_item].descr,//商品名称
							qtyreceived:that.Consignment_CheckDetailsData[that.detail_item].qtyreceived,//已收数量
							qtyremain:that.Consignment_CheckDetailsData[that.detail_item].qtyremain,//待收数量
							sku:that.Consignment_CheckDetailsData[that.detail_item].sku,//商品代码
							susr2:that.Consignment_CheckDetailsData[that.detail_item].susr2,//单位
						}
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.CheckDetails{
		&>.container{
			width:100%;
			font-size: 30/50rem;
			&>div{
				overflow: hidden;
				width:1300/50rem;
				height: 80/50rem;
				line-height: 80/50rem;
				border-bottom: 1/50rem solid #999;
				text-align: center;
				&>span{
					float: left;
					height:100%;
					width:260/50rem;
					text-align:center;
				}
				&>span:last-child{
					width:500/50rem;
				}
			}
			.iconfont{
				font-size: 40/50rem;
			}
		}
	}
</style>


