<template>

	<div class="RFOrderShip">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>发运</span><i class="iconfont icon-saoma" @click="scan"></i></header>

		<div class="container">

			<div>
				<span>发运单号</span>
				<input class="writable"  @focus="focus($event)" :min="1" :max="10" ref="OrderKey" placeholder="请输入内容" v-model="OrderKey" @keyup.enter="RF_OrderQuery"></input>
				<i v-if="OrderKey" @click="clearInput('OrderKey')" class="iconfont icon-cuowu"></i>
			</div>

			<div>
				<span>预期日期</span>
				<input v-model="RequestedShipDate" readonly></input>
				<i v-if="RequestedShipDate"</i>
			</div>

			<div>
				<span>　收货方</span>
				<input v-model="C_company" readonly></input>
				<i v-if="C_company"></i>
			</div>


			<div class="address">
				<span>　　地址</span>
				<textarea v-model="C_address1" readonly></textarea>
			</div>

			<div>
				<span>　总数量</span>
				<input v-model="QtyTotal" readonly></input>
				<i v-if="QtyTotal"></i>
			</div>

			<div>
				<span>　总体积</span>
				<input v-model="TotalVol" readonly></input>
				<i v-if="TotalVol"></i>
			</div>

			<div>
				<span>　总重量</span>
				<input v-model="TotalWgt" readonly></input>
				<i v-if="TotalWgt"></i>
			</div>

			<div class="g_doubleBtn">
				<div ref="RF_Btn_OrderShip" @click="RF_OrderShip">发运确认</div>
				<div @click="RF_OrderDetailQuery">查看明细</div>
			</div>

		</div>

		<div v-if="tipsHide" @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @ref="inputFocus" @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>

	</div>
</template>
<script type="text/javascript">

	export default{
		name:"RFOrderShip",
		data(){
			return{   
        OrderKey:'', // 0000003356
        C_company:'',
        C_address1:'',
        QtyTotal:'',
        TotalVol:'',
        TotalWgt:'',
        RequestedShipDate:'',
        Status:0,
        tipsHide:false,//提示信息是否显示
        inputFocus:"",
        tokenString:"",
        SkuCnt:0,
    }
},
mounted(){

	// this.$refs.OrderKey.focus();
	// var that = this;

	// document.addEventListener('touchstart', function(e) {  

	// 	that.focusOrderShipBtn(false);
	// });  
	// window.QRScanAjax = this.QRScanAjax;


	// // 当光标聚焦在 "发运确认" 回车后触发接口RF_OrderShip
	// var that = this;
	// document.onkeyup = function(e){
	// 	var code = e.charCode || e.keyCode;
	// 	if(code == 13) {
	// 		// 光标聚焦在 "发运确认" 按钮时
	// 		if(that.$refs.RF_Btn_OrderShip.style.backgroundColor == "rgb(0, 0, 139)"){

	// 			// 总数量为空，先执行 "发运订单查询"
 //      			if(!that.QtyTotal) {

 //      				that.RF_OrderQuery(true);
 //      			}else {

	// 				that.focusOrderShipBtn(false);
 //      				that.RF_OrderShip_commit();
 //      			}
	// 		}
	// 	}
	// }


},
created(){
	
	this.OrderKey = this.$route.query.OrderKey;
	this.RequestedShipDate = this.$route.query.RequestedShipDate;
	this.C_company = this.$route.query.C_company;
	this.C_address1 = this.$route.query.C_address1;
	this.QtyTotal = this.$route.query.QtyTotal;
	this.TotalVol = this.$route.query.TotalVol;
	this.TotalWgt = this.$route.query.TotalWgt; 
},
destroyed(){
	// 销毁屏幕监听（无效），原因不能是匿名函数
	document.removeEventListener('touchstart', function(e) {  

	});
},
methods:{
	scan:function (){
		try {

    		// CallAndroidOrIOS("我想调用app原生扫描二维码/条码");
    		CallAndroidOrIOS.callAndroid("我想调用app原生扫描二维码/条码");
		} 
		catch(error) {

			// console.log("我没有CallAndroidOrIOS方法")
			console.log("我没有CallAndroidOrIOS.callAndroid方法")
		}
	},
	// 接收iOS传值到vue，实现移动端方法
	QRScanAjax:function (qrcode){

        this.OrderKey = qrcode;
    },
	focus(e) {
		
		// event.currentTarget.select();
	},
	// 清空输入框
	clearData:function(){

		this.RequestedShipDate = "";
		this.C_company = "";
		this.C_address1 = "";
		this.QtyTotal = "";
		this.TotalVol = "";
		this.TotalWgt = "";
		this.Status = "";
	},
	// 光标聚集到确认按钮
	focusOrderShipBtn:function(focus){

		if(this.$refs["RF_Btn_OrderShip"]){

			this.$refs["RF_Btn_OrderShip"].style.backgroundColor = focus ? "#00008B" : "#285DCE";
		}
	},
    // 查询发运订单，isfocuRF_Btn_OrderShip 光标是否聚焦到确认按钮
    RF_OrderQuery:function(isfocuRF_Btn_OrderShip){

    	var that = this;
		this.clearData();
		this.$refs.OrderKey.blur();

    	if(!this.OrderKey){

    		this.msg = "发货单号不能为空";
    		that.tipsShow("OrderKey");
    		that.inputFocus = "OrderKey";
    	}else{
    		var currentUrl = this.HOST + "RFOrderQuery.do";

      		var postData = {

      			"OrderKey":this.OrderKey,
      		}

      		this.httpRequest(currentUrl,postData,function(res){

      			if(res.data.data.length == 0){

      				that.msg = "订单待发运，发运单号:" + that.OrderKey;
      				that.tipsShow("OrderKey");
      				that.inputFocus = "OrderKey";
      			}else{

      				that.RequestedShipDate = res.data.data[0].RequestedShipDate;
      				that.C_company = res.data.data[0].C_company;
      				that.C_address1 = res.data.data[0].C_address1;
      				that.QtyTotal = res.data.data[0].QtyTotal;
      				that.TotalVol = res.data.data[0].TotalVol;
      				that.TotalWgt = res.data.data[0].TotalWgt;
      				that.Status = res.data.data[0].Status;
      				that.SkuCnt = res.data.data[0].SkuCnt;

      				if(that.Status < 14){

      					that.msg = "发货单未分配库存，不能发运";
      					that.tipsShow("OrderKey");
      					that.inputFocus = "OrderKey";
      				}else if(that.Status == 95){

      					that.msg = "发货单已经发运";
      					that.tipsShow("OrderKey");
      					that.inputFocus = "OrderKey";
      				}else if(that.Status > 95){

      					that.msg = "发货单已经取消，不能发运";
      					that.tipsShow("OrderKey");
      					that.inputFocus = "OrderKey";
      				}else{

      					that.focusOrderShipBtn(isfocuRF_Btn_OrderShip);
      				}
      			}
      		},"OrderKey")
      	}
    },
    // 发运确认(第一步)
    RF_OrderShip(){

      	var that = this;

      	if(!this.OrderKey){

      		this.msg = "请输入【发货单号】";
      		that.tipsShow("OrderKey");
      		that.inputFocus = "OrderKey";
      	}else{

      		// 总数量为空，先执行 "发运订单查询"
      		if(!this.QtyTotal) {

      			this.RF_OrderQuery(true);
      		}else {
      			this.RF_OrderShip_commit();
      		}
      	}
    },
    // 发运确认(第二步)
    RF_OrderShip_commit(){

      	var that = this;
      	var currentUrl = this.HOST + "RFOrderShip.do";

      	var postData = {

      		"OrderKey":this.OrderKey,
      	}

      	this.httpRequest(currentUrl,postData,function(res){

      		that.clearData();
      		that.OrderKey = "";
      		that.$refs.OrderKey.focus();
      	},"OrderKey")

    },
      // 查看明细
    RF_OrderDetailQuery(){

      	var that = this;

      	if(!this.OrderKey){

      		this.msg = "请输入【发货单号】";
      		that.tipsShow("OrderKey");
      		that.inputFocus = "OrderKey";
      	}else{

      		if(!this.QtyTotal) {

      			this.RF_OrderQuery(false);
      		}else{


      			var currentUrl = this.HOST + "RFOrderDetailQuery.do";

      			var postData = {

      				"OrderKey":this.OrderKey,
      			}

      			this.httpRequest(currentUrl,postData,function(res){
      				
					that.$store.state.RFOrderShip_RFOrderDetailQuery = res.data.data;

      				that.$router.push({
      					name:"RFOrderDetailQuery",
      					query:{
      						OrderKey:that.OrderKey,
      						RequestedShipDate:that.RequestedShipDate,
      						C_company:that.C_company,
      						C_address1:that.C_address1,
      						QtyTotal:that.QtyTotal,
      						TotalVol:that.TotalVol,
      						TotalWgt:that.TotalWgt,
      						SkuCnt:that.SkuCnt,
      					}
      				})
      			},"OrderKey")
      		}
      	}
      },
    //返回上一页  首页
    toIndex:function(){
      	this.$router.push({
      		name:"Index",
      	})
    },
  }
}
</script>
<style scoped lang="less">
.RFOrderShip{
	.container{
		font-size: 30/50rem;
		input{
			font-size: 25/50rem;
			background-color: #ccc;
		}
		&>div{
			width: 100%;
			height: 80/50rem;
			margin-top: 20/50rem;
			padding: 25/50rem 20/50rem;
			box-sizing: border-box;
			position: relative;

			span{
				width: 140/50rem;
				text-align: right;
			}

			input::placeholder{
				font-size: 30/50rem;
			}
			
			input{
				margin-left: 10/50rem;
				width: 490/50rem;
				height: 55/50rem;
				border-radius: 5/50rem;
				border: 1/50rem solid #aaa;
				outline: none;
				padding: 0 10/50rem;
				font-size: 30/50rem;
			}
			i{
				position:absolute;
				right:90/50rem;
				font-size: 30/50rem;
				top: 36/50rem;
			}
		}
		.address{

			&>span{

				width: 130/50rem;
				text-align: right;
				float: left;
				margin-left: -10/50rem;
				margin-right: 10/50rem;
			}

			&>textarea{

				width:486/50rem;
				height:70/50rem;
				padding:0 10/50rem;
				margin-left: 10/50rem;
				background-color: #ccc;
			}
		}

		.writable{
			background-color: #fff;
		}
	}

	.commitBtn{

		background-color: #228B22;
		margin-left: 0/50rem;
		margin-right: 0/50rem;
		margin-top: 0/50rem;
		margin-bottom: 0/50rem;
		width: 100%;
		height: 100%;
		z-index:100;
	}
}

@media screen and (max-width: 240px){
	.RFOrderShip{
		.container{
			font-size: 30/50rem;
			input::placeholder{
				font-size: 25/50rem;
			}
			input{
				font-size: 25/50rem;
			}
			&>div{
				span{
					width: 140/50rem;
					text-align: right;
				}
				i{
					right:70/50rem;
				}
			}

			.address{

				&>span{

					width: 160/50rem;
					text-align: right;
					float: left;
					margin-left: -10/50rem;
					margin-right: 22/50rem;
				}

				&>textarea{

					width:486/50rem;
					height:70/50rem;
					padding:0 10/50rem;
					margin-left: 10/50rem;
				}
			}

		}
	}
}

.RFOrderShip{
	.container{
		&>div{
			height: 60/50rem;
			margin-top: 17/50rem;
		}
		&>div:nth-child(4),&>div:nth-child(5){
			margin-top: 25/50rem;
		}
		.address{
			padding-top: 17/50rem;
		}
	}
}
</style>


