<template>
	<div class="SelectOrder">
		<header><i class="iconfont icon-xiangzuo1" @click="toCycleCount"></i><span>选择盘点单</span></header>
		<div class="container">
			<div>
				<span>选择</span>
				<span>盘点单号</span>
				<span>盘点类型</span>
				<span>创建日期</span>
				<span>创建人</span>
				<span>状态</span>
			</div>

			<div v-for='(dataItem,index) in CycleCount_RFCountSel'  :id="index"  :key='index'  @click="selectData(index)">
				<span class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></span>
				<span>{{dataItem.CountNo}}</span>
				<span>{{dataItem.CountType}}</span>
				<span>{{dataItem.AddDate}}</span>
				<span>{{dataItem.AddWho}}</span>
				<span>{{dataItem.CountStatus}}</span>
			</div>
			<p class="positionFixed"><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"SelectOrder",
		data(){
			return{
				CycleCount_RFCountSel:[],//列表数据
				detail_item:0,//默认为第一个
			}
		},
		created(){
			console.log(this.$store.state.CycleCount_RFCountSel)

			var CycleCount_RFCountSel = this.$store.state.CycleCount_RFCountSel;

			this.CycleCount_RFCountSel = CycleCount_RFCountSel;
		},
		methods:{
			// 返回上一页 盘点页面
			toCycleCount:function(){

				this.$router.push({
					name:"CycleCount",
				})
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;

				
			},
			// 点击确认按钮 跳转到盘点页面
			selectData_submit:function(){


				var CountNo = this.CycleCount_RFCountSel[this.detail_item].CountNo       //盘点单号
				var CountType =  this.CycleCount_RFCountSel[this.detail_item].CountType  //盘点类型

				this.$router.push({
					name:"CycleCount",
					query:{
						SelectOrder:{
							CountNo:CountNo,
							CountType:CountType
						}
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
.SelectOrder{
	&>.container{
		width:100%;
		font-size: 30/50rem;
		&>div{
			overflow: hidden;
			width:800/50rem;
			height: 80/50rem;
			line-height: 80/50rem;
			border-bottom: 1/50rem solid #999;
			text-align: center;
			&>span{
				float: left;
				height:100%;
				width:80/50rem;
				text-align:center;
			}
			&>span:nth-child(1),&>span:nth-child(2),&>span:nth-child(3){
				width:200/50rem;
			}
			&>span:nth-child(5){
				width:80/50rem;
			}
		}
	}
}
@media screen and (min-width: 240px){
	.SelectOrder{
		&>.container{
			width:100%;
			font-size: 30/50rem;
			&>div{
				overflow: hidden;
				width:1000/50rem;
				height: 80/50rem;
				line-height: 80/50rem;
				border-bottom: 1/50rem solid #999;
				text-align: center;
				&>span{
					float: left;
					height:100%;
					width:80/50rem;
					text-align:center;
				}
				&>span:nth-child(1){
					width:140/50rem;
				}
				&>span:nth-child(2){
					width:150/50rem;
				}
				&>span:nth-child(3){
					width:350/50rem;
				}
				&>span:nth-child(4){
					width:350/50rem;
				}
				&>span:nth-child(6){
					width:140/50rem;
				}
				.iconfont{
					font-size: 40/50rem;
				}
				.icon-xuanzhong{
					color:#285DCE;
				}
			}
			
			&>p.positionFixed{
				width: 100%;
				height:130/50rem;
				position: fixed;
				bottom: 0;

			}
		}
	}
}
</style>


