<template>
	<div class="RFInvMove">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>库存移动</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>
				<span>　自库位</span>
				<input ref="Loc" placeholder="请输入内容" @keyup.enter="SkipToLoc" v-model="Loc" ></input>
				<i v-if="Loc" @click="clearInput('Loc')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>自托盘号</span>
				<input placeholder="请输入内容" v-model="PID"></input>
				<i v-if="PID" @click="clearInput('PID')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>商品代码</span>
				<input placeholder="请输入内容" v-model="Sku"></input>
				<i v-if="Sku" @click="clearInput('Sku')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>　至库位</span>
				<input ref="ToLoc" placeholder="请输入内容" @keyup.enter="SkipQty" v-model="ToLoc"></input>
				<i v-if="ToLoc" @click="clearInput('ToLoc')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>至托盘号</span>
				<input  placeholder="请输入内容" v-model="ToPID"></input>
				<i v-if="ToPID" @click="clearInput('ToPID')" class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span>移动数量</span>
				<input @focus="focus($event)" :min="1" :max="10"  ref="Qty" placeholder="请输入内容" @keyup.enter="MoveInventory" v-model="Qty"></input>
				<i v-if="Qty" @click="clearInput('Qty')" class="iconfont icon-cuowu"></i>
			</div>
			<div class="prompt">
				<span>0 - 表示全部可用数量</span>
			</div>
			<div class="g_doubleBtn">
				<div @click="MoveInventory">执行移动</div>
				<div @click="viewInventory">查看库存</div>
			</div>
		</div>
		
		<div v-if="tipsHide"  @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p @ref="inputFocus" @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
		
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"InventoryQuery",
		data(){
			return{
				Loc:"",//6010110
				PID:"",//P1804260001
				Sku:"",//1010001000
				ToLoc:"",//6010120
				ToPID:"",//P1805150002
				Qty:'0',
				listData:{},
				tipsHide:false,//提示信息是否显示
				msg:'',//
				inputFocus:"",
			}
		},
		created(){

			if(this.global.RFInvMoveCondition){

				var RFInvMoveCondition = this.global.RFInvMoveCondition;

				this.Loc = RFInvMoveCondition.Loc;
				this.PID = RFInvMoveCondition.PID;
				this.Sku = RFInvMoveCondition.Sku;
				this.ToLoc = RFInvMoveCondition.ToLoc;
				this.ToPID = RFInvMoveCondition.ToPID;
				this.Qty = RFInvMoveCondition.Qty;
			}
		},
		mounted() {

			this.$refs.Loc.focus();
		},
		methods:{

		// 清空数据
		clearData:function(){

			this.Loc = "";
			this.PID = "";
			this.Sku = "";
			this.ToLoc = "";
			this.ToPID = "";
			this.Qty = "";
		},
		focus(e) {
			event.currentTarget.select();
		},
 		// 跳转光标到 至库位		
 		SkipToLoc:function(){	

 			this.$refs.ToLoc.focus();		
 		},		
 		// 跳转光标到 数量		
 		SkipQty:function(){	

 			this.$refs.Qty.focus();	

 		},	
 		// 移动库存
 		MoveInventory:function(){

 			var that = this;

 			if(!this.Loc || !this.ToLoc) {

 				that.msg = "自库位、至库位不能为空";
				that.tipsShow("Loc");
				that.inputFocus = "Loc";
 			}else{

 				if(this.Qty < 0) {

 					that.msg = "移动数量必须大于或等于0";
					that.tipsShow("Qty");
					that.inputFocus = "Qty";
 				}else{

 					var currentUrl = this.HOST + "RFInvMove.do";

 					var postData = {
 						"Loc":this.Loc,
 						"Sku":this.Sku,
 						"PID":this.PID,
 						"ToLoc":this.ToLoc,
 						"ToPID":this.ToPID,
 						"Qty":this.Qty,
 					}

 					this.httpRequest(currentUrl,postData,function(res){

 						that.clearData();
 					},"Loc")
 				}
 			}
 		},
			// 查看库存
			viewInventory:function(){

				var that = this;

				var currentUrl = this.HOST + "RFInvQuery.do";

				var postData = {
					"Loc":this.Loc,
					"Sku":this.Sku,
					"PID":this.PID,
				}


				this.httpRequest(currentUrl,postData,function(res){
					
					that.global.RFInvMoveCondition = {
						Loc:that.Loc,
						PID:that.PID,
						Sku:that.Sku,
						ToLoc:that.ToLoc,
						ToPID:that.ToPID,
						Qty:that.Qty,
					}

					that.global.InventoryQuery = false;
					that.global.RFInvMove = true;
					
						//跳转到库存详情页面
						that.$router.push({
							name:"InventoryDetails",
							query:{

							}
						})

					that.global.QueryListData = res.data.data;
				},"Loc")
			},
			//返回上一页  首页
			toIndex:function(){
				this.$router.push({
					name:"Index",
				})
			}
		}
	}
</script>
<style scoped lang="less">
.RFInvMove{
	.container{
		font-size: 30/50rem;
		padding-top: 1/50rem;
		input::placeholder{
			font-size: 25/50rem;
		}
		input{
			font-size: 25/50rem;
		}
		&>div{
			width:100%;
			height: 55/50rem;
			margin-top: 20/50rem;
			padding: 0 20/50rem;
			box-sizing: border-box;
			position: relative;

			span{
				margin-left: 5/50rem;
				text-align: right;
			}

			input{
				margin-left: 10/50rem;
				width:490/50rem;	
				height:55/50rem;
				padding:0 10/50rem;
			}
			i{
				position:absolute;
				right:90/50rem;
				font-size: 28/50rem;
				top: 13/50rem;
			}
		}
		.prompt{
			margin-left: 140/50rem;
			color:#006400;
			font-size: 28/50rem;
		}
	}

}
</style>


