<template>
	<div class="TakeDelivery">
		<header><i class="iconfont icon-xiangzuo1" @click="ToReceipt"></i><span>收货</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>
				<div>
					<span>收货单号</span>
					<input    class="readonly_input" readonly  v-model="input_ReceiptNum"></input>
				</div>
				<div>
					<span>客户单号</span>
					<input    class="readonly_input"  readonly  v-model="input_CustomerNum"></input>
				</div>
			</div>
			<div class="sousuo">
				<div>
					<span>商品代码</span>
					<input    ref="input_CommodityCode"  @keyup.enter="SkuInfo"   v-model="input_CommodityCode"></input>
					<i v-if="input_CommodityCode" @click="clearInput('input_CommodityCode')"  class="iconfont icon-cuowu"></i>
					<i class="iconfont icon-sousuo"  @click="ToSummary"></i>
				</div>
				<div>
					<span>客户代码</span>
					<input    class="readonly_input"  readonly  v-model="input_CustomerCode"></input>
				</div>
			</div>

			<div class="goodsName">
				<span>商品名称</span>
				<textarea  class="readonly_input" v-model="input_CommodityName"   readonly></textarea>
			</div>
			<div>
				<div>
					<span>待收数量</span>
					<input    class="readonly_input"  readonly  v-model="input_toBeReceived"></input>
				</div>
				<div>
					<span>已收数量</span>
					<input    class="readonly_input"   readonly  v-model="input_AmountReceived"></input>
				</div>
			</div>
			<div>
				<div>
					<span>数量</span>
					<input type="number" @keyup.enter="getFocus('input_batchNum')"  ref="input_num"  placeholder="必填"   v-model="input_num"></input>
					<i v-if="input_num" @click="clearInput('input_num')"  class="iconfont icon-cuowu"></i>
				</div>
				<div>
					<span>单位</span>
					<input    class="readonly_input"  readonly  v-model="input_unit"></input>
				</div>
			</div>
			<div>
				<div>
					<span>生产批号</span>
					<input ref="input_batchNum" @keyup.enter="getFocus('input_DateofManufacture')"     v-model="input_batchNum"></input>
					<i v-if="input_batchNum" @click="clearInput('input_batchNum')"  class="iconfont icon-cuowu"></i>
				</div>
				<div>
					<span>货物状态</span>
					<input     v-model="input_StateOfGoods"></input>
					<i v-if="input_StateOfGoods" @click="clearInput('input_StateOfGoods')"  class="iconfont icon-cuowu"></i>
				</div>
			</div>
			<div>
				<div>
					<span>生产日期</span>
					<input   ref="input_DateofManufacture"  @keyup.enter="getFocus('input_PalleNumber')"    v-model="input_DateofManufacture"></input>
					<i v-if="input_DateofManufacture" @click="clearInput('input_DateofManufacture')"  class="iconfont icon-cuowu"></i>
				</div>
				<div>
					<span>到期日期</span>
					<input   ref="input_ExpirationDate"    v-model="input_ExpirationDate"></input>
					<i v-if="input_ExpirationDate" @click="clearInput('input_ExpirationDate')"  class="iconfont icon-cuowu"></i>
				</div>
			</div>
			<div>
				<div>
					<span>托盘号</span>
					<input   ref="input_PalleNumber" @keyup.enter="submit"    v-model="input_PalleNumber"></input>
					<i v-if="input_PalleNumber" @click="clearInput('input_PalleNumber')"  class="iconfont icon-cuowu"></i>
				</div>
				<div>
					<span>库位代码</span>
					<input     v-model="input_LibraryCode"></input>
					<i v-if="input_LibraryCode" @click="clearInput('input_LibraryCode')"  class="iconfont icon-cuowu"></i>
				</div>
			</div>
			<span class="g_singleBtn" @click="submit">确认</span>
			<p class="warningInfo">{{warningInfo}}</p>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @ref="inputFocus" @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"TakeDelivery",
		data(){
			return{
				input_ReceiptNum:'',
				input_CustomerNum:'',
				input_CommodityCode:'',//商品代码 1010001000
				input_CustomerCode:'',
				input_CommodityName:'',
				input_toBeReceived:'',
				input_AmountReceived:'',
				input_num:'',
				input_unit:'',
				input_batchNum:'',
				input_StateOfGoods:'NR',
				input_DateofManufacture:'',
				input_ExpirationDate:'',
				input_PalleNumber:'',
				input_LibraryCode:'STAGE',
				warningInfo:"",//警告信息
				tipsHide:false,//提示信息是否出现
				msg:"",//提示信息内容
				roterPath:"",//上一页路由名称
				inputFocus:"",
			}
		},
		mounted() {

			var that = this;

			// 上一页为  选择商品 
			if(this.$route.query.Summary){

				this.$refs['input_num'].focus();

			//上一页为   录入单号
			}else{

				this.$refs['input_CommodityCode'].focus();

			}
		}, 
		created(){

			var that = this;

			if(that.$store.state.Receipt_SubmitData.length){

				var Receipt_SubmitData = that.$store.state.Receipt_SubmitData;
				
				that.input_ReceiptNum = Receipt_SubmitData[0].receiptkey;

				that.input_CustomerNum = Receipt_SubmitData[0].externalreceiptkey2;

				that.input_CustomerCode = Receipt_SubmitData[0].storerkey;
			}

			if(that.$route.query.Summary){

				var SummaryData = that.$route.query.Summary;

				that.input_CommodityName = SummaryData.descr; //商品名称

				that.input_AmountReceived = SummaryData.qtyreceived; //已收数量

				that.input_toBeReceived = SummaryData.qtyremain; //待收数量

				that.input_CommodityCode = SummaryData.sku; //商品代码

				that.input_unit = SummaryData.susr2; //单位

			}

		},
		methods:{
			// 返回上一页
			ToReceipt:function(){

				var that = this;

				this.$router.push({
					name:"Receipt",
					query:{
						ReceiptKey:that.input_ReceiptNum,
						ExternalReceiptKey2: that.input_CustomerNum
					}
				})
			},
			// 跳转到 选择商品页面
			SkuInfo:function(){

				var that = this;

				if(!this.input_CommodityCode){

					that.warningInfo = "请填写商品代码";

					return;

				}

				var SkuUrl = this.HOST + "RFReceiveSku.do";

				var SkuData = {
					ReceiptKey : this.input_ReceiptNum,
					StorerKey : this.input_CustomerCode,
					Sku:this.input_CommodityCode
				}

				this.httpRequest(SkuUrl,SkuData,function(res){

					var resultData = res.data.data[0];

					that.input_CommodityName = resultData.descr; //商品名称

					that.input_toBeReceived = resultData.qtyremain; //待收数量

					that.input_AmountReceived = resultData.qtyreceived; //已收数量

					that.input_unit = resultData.susr2; //单位

					// 聚焦
					that.getFocus("input_num");
				},"input_CommodityCode")
			},
			// 收货确认 提交
			submit:function(){

				var that = this;

				if(!this.input_CommodityCode){

					that.warningInfo = "请填写商品代码";
					that.getFocus("input_CommodityCode");

					return;

				}else if(!this.input_num){

					that.warningInfo = "请填写数量";
					that.getFocus("input_num");

					return;

				}else{

					that.warningInfo = ""
				}

				// 生产日期
				if(that.input_DateofManufacture.length){

					that.input_DateofManufacture = that.ValidationTimeFormat(that.input_DateofManufacture);

					if(!that.input_DateofManufacture){

						that.getFocus("input_DateofManufacture");
						that.input_DateofManufacture = "";
						return
					}

				// 到期日期
				}else if(that.input_ExpirationDate){

					that.input_ExpirationDate = that.ValidationTimeFormat(that.input_ExpirationDate);

					if(!that.input_ExpirationDate){

						that.getFocus("input_ExpirationDate");
						that.input_ExpirationDate = "";
						return
					}
				}
				
				var RFReceiveUrl = this.HOST + "RFReceive.do";

				var r_postData = {
					ReceiptKey : this.input_ReceiptNum,
					StorerKey : this.input_CustomerCode,
					Sku : this.input_CommodityCode,
					Qty : this.input_num,
					Lottable03 : this.input_batchNum,
					Lottable02 : this.input_StateOfGoods,
					Lottable04 : this.input_DateofManufacture,
					Lottable05 : this.input_ExpirationDate,
					PID : this.input_PalleNumber,
					Loc : this.input_LibraryCode,
				}


				this.httpRequest(RFReceiveUrl,r_postData,function(res){

					that.input_CommodityName = ""; //商品名称
					that.input_AmountReceived = ""; //已收数量
					that.input_toBeReceived = ""; //待收数量
					that.input_CommodityCode = ""; //商品代码
					that.input_unit = ""; //单位
					that.input_num = ""; //数量
					that.input_batchNum = ""; //生产批号
					that.input_DateofManufacture = ""; //生产日期
					that.input_ExpirationDate = ""; //到期日期
					that.input_PalleNumber = ""; //托盘号

					// 光标聚焦
					that.getFocus("input_CommodityCode");
				},"input_CommodityCode")
			},
			//处理时间格式
			ValidationTimeFormat:function(time){

				var that = this;

				var year = new Date().getFullYear() + '';

				if(time.length==4 || time.length==6 || time.length==8 || time.length==10 ){

					if(time.length==10){

						if(time[4]=="-" && time[7]=="-"){

							that.warningInfo = "";

							return time;

						}else{

							that.warningInfo = "请填写正确的日期格式 ; 例如20180524 , 或2018-05-24";

							return;
						}
					}else if(time.length==4){

						return year + "-" + time.substring(0,2) + "-" + time.substring(2,4);

					}else if(time.length==6 ){

						return year.substring(0,2) +  time.substring(0,2) + "-" + time.substring(2,4) + "-" + time.substring(4,6);

					}else if(time.length==8 ){

						return  time.substring(0,4) + "-" + time.substring(4,6) + "-" + time.substring(6,8);

					}

				}else{

					that.warningInfo = "请填写正确的日期格式 ; 例如20180524 , 或2018-05-24";

					return;
				}
			},
			//跳转到汇总页面（公用页面）  选择商品
			ToSummary:function(){

				var that = this;

				var SummaryUrl = this.HOST + "RFReceiptSum.do";

				var sum_Data = {
					ReceiptKey : this.input_ReceiptNum,
					ExternalReceiptKey2 : this.input_CommodityCode
				}

				this.httpRequest(SummaryUrl,sum_Data,function(res){

					if(res.data.data.length){
							
						that.$store.state.Receipt_SummaryData = res.data.data;

						that.$router.push({
							name:"Summary",
							query:{
								title:"选择商品",
								ifSelect:true,
								TakeDelivery:{
									ExternalReceiptKey2 : that.input_CommodityCode
								}
							}
						})
					}else{
						that.tipsShow("input_ReceiptNum");

						that.msg = "没有数据";

						that.inputFocus = "input_ReceiptNum";
					}
				},"input_CommodityCode")
			},
		}
	}
</script>
<style lang="less" scoped>
	.TakeDelivery{
		.container{
			font-size: 29/50rem;
			padding-top: 20/50rem;
			input{
				font-size: 25/50rem;
			}
			&>div{
				width:100%;
				height: 70/50rem;
				box-sizing: border-box;
				overflow: hidden;
				&>div{
					width:400/50rem;
					float: left;
					position: relative;
					span{
						width: 130/50rem;
						margin-right: 10/50rem;
						float: left;
						text-align: right;
					}
					input{
						width:230/50rem;
						float: left;
						height:45/50rem;
						padding-left: 5/50rem;
						box-sizing: border-box;
					}
					.icon-cuowu{
						position:absolute;
						right:35/50rem;
						font-size: 28/50rem;
						top: 8/50rem;
						color: #bbb;
					}
				}
				&>div:nth-child(2){
					width:350/50rem;
					input{
						width:180/50rem;
					}
				}
			}
			&>.sousuo{
				&>div:nth-child(1){
					width:420/50rem;
					.icon-sousuo{
						margin-left: 10/50rem;
						font-size: 35/50rem;
					}
					.icon-cuowu{
						right:65/50rem;
					}
				}
				&>div:nth-child(2){
					width:315/50rem;
					input{
						width:159/50rem;	
					}
				}
			}
			&>.goodsName{
				height:100/50rem;
				margin-bottom: 15/50rem;
				overflow: hidden;
				&>span{
					width: 130/50rem;
					text-align: right;
					float: left;
				}
				&>textarea{
					width:580/50rem;
					height:100/50rem;
					float:left;
					outline:none;
					margin-left: 10/50rem;
				}
			}
			.readonly_input{
				background-color: #F0F0F0;
			}
		}
	}

    @media screen and (max-width: 329px) and (min-width: 291px) {
   		.TakeDelivery{
			.container{
				padding-top: 20/50rem;
				input{
					font-size: 25/50rem;
				}
				&>div{
					height: 55/50rem;
					line-height: 55/50rem;
					&>div{
						width:380/50rem;
						span{
							width: 130/50rem;
							margin-right: 10/50rem;
						}
						input{
							height:50/50rem;
							padding:0 10/50rem;
							box-sizing: border-box;
						}
					}
					&>div:nth-child(2){
						width:350/50rem;
						input{
							width:200/50rem;	
						}
					}
				}
				&>.goodsName{
					margin-bottom: 5/50rem;
					&>span{
						width: 130/50rem;
						margin-right: 0;
					}
					&>textarea{
						font-size: 25/50rem;
						padding:0 10/50rem;
						box-sizing: border-box;
					}
				}
			}
		}
    }

    @media screen and (max-width: 290px){
    	.TakeDelivery{
			.container{
				padding-top: 20/50rem;
				input{
					font-size: 25/50rem;
				}
				&>div{
					height: 70/50rem;
					&>div{
						width:368/50rem;
						span{
							width: 150/50rem;
							margin-right: 0;
						}
						input{
							width:212/50rem;
							height:50/50rem;
						}
					}
					&>div:nth-child(2){
						width:380/50rem;
						input{
							width:220/50rem;	
						}
					}
				}
				&>.goodsName{
					&>span{
						width: 150/50rem;
						text-align:right;
						margin-right: 0;
					}
					&>textarea{
						font-size: 25/50rem;
						padding:0 10/50rem;
						margin-left: 0;
						box-sizing: border-box;
					}
				}
				&>.sousuo{
					&>div:nth-child(2){
						width:320/50rem;
						input{
							width:164/50rem;	
						}
					}
				}
			}
		}
    }
</style>


