<template>
	<div class="rf_class_OrderDetailQuery">
		<header><i class="iconfont icon-xiangzuo1" @click="toRFOrderShip"></i><span>发运-查看明细</span></header>
		<div class="container">
			<div>
				<span>商品代码</span>
				<span>数量</span>
				<span>箱数</span>
				<span>商品名称</span>
				<span>单位</span>
			</div>

			<div v-for='(dataItem,index) in RFOrderShip_RFOrderDetailQuery'  :id="index"  :key='index'  @click="selectData(index)">
				<span>{{dataItem.Sku}}</span>
				<span>{{dataItem.qty}}</span>
				<span>{{dataItem.QtyCase}}</span>
				<span>{{dataItem.Descr}}</span>
				<span>{{dataItem.Susr2}}</span>
			</div>


			<div class="bottomNum">
				<div>
					<span>总数量</span>
					<input v-model="QtyTotal"></input>
				</div>
				<div>
					<span>商品数</span>
					<input v-model="SkuCnt"></input>
				</div>
			</div>

		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"RFOrderDetailQuery",
		data(){
			return{

				RFOrderShip_RFOrderDetailQuery:[],// 列表数据
				QtyTotal:0, // 总数量
				SkuCnt:0,   // 商品数
			}
		},
		created(){


			// 获取从查询页面传来的数据
			this.QtyTotal = this.$route.query.QtyTotal;
			this.SkuCnt = this.$route.query.SkuCnt;
			

			console.log(this.$store.state.RFOrderShip_RFOrderDetailQuery)

			var RFOrderShip_RFOrderDetailQuery = this.$store.state.RFOrderShip_RFOrderDetailQuery;

			this.RFOrderShip_RFOrderDetailQuery = RFOrderShip_RFOrderDetailQuery;
		},
		methods:{
			// 返回上一页 发运页面
			toRFOrderShip:function(){

				this.$router.push({
					name:"RFOrderShip",
					query:{
						OrderKey:this.$route.query.OrderKey,
						RequestedShipDate:this.$route.query.RequestedShipDate,
						C_company:this.$route.query.C_company,
						C_address1:this.$route.query.C_address1,
						QtyTotal:this.$route.query.QtyTotal,
						TotalVol:this.$route.query.TotalVol,
						TotalWgt:this.$route.query.TotalWgt,
					}
				})
			},
		}
	}
</script>
<style lang="less" scoped>
.rf_class_OrderDetailQuery{
	&>.container{
		width:100%;
		font-size: 30/50rem;
		&>div{
			overflow: hidden;
			width:970/50rem;
			height: 80/50rem;
			line-height: 80/50rem;
			border-bottom: 1/50rem solid #999;
			text-align: center;
			&>span{
				float: left;
				height:100%;
				width:80/50rem;
				text-align:center;
			}
			&>span:nth-child(1){
				width:220/50rem;
			}
			&>span:nth-child(2){
				width:100/50rem;
			}
			&>span:nth-child(3){
				width:200/50rem;
			}
			&>span:nth-child(4){
				width:270/50rem;
			}
			&>span:nth-child(5){
				width:180/50rem;
			}
		}
	}

	.bottomNum{
		width: 100%;
		height: 80/50rem;
		line-height: 80/50rem;
		position: fixed;
		left: 0;
		bottom: 0;
		background-color: #fff;
		z-index: 5;
		&>div{
			float: left;
			width: 375/50rem;
			height: 60/50rem;
			span{
				float:left;
				height: 60/50rem;
				margin-left: 75/50rem;
				margin-right: 10/50rem;
			}
			input{
				width: 150/50rem;
				text-align: center;
				height: 40/50rem;
				font-size: 29/50rem;
				background-color:#f0f0f0;
			}
		}

	}
}

@media screen and (min-width: 240px){
	.rf_class_OrderDetailQuery{
		&>.container{
			&>span:nth-child(1){
				width:270/50rem;
			}
			&>span:nth-child(2){
				width:100/50rem;
			}
			&>span:nth-child(3){
				width:200/50rem;
			}
			&>span:nth-child(4){
				width:350/50rem;
			}
			&>span:nth-child(5){
				width:180/50rem;
			}
		}
	}
}
</style>


