<template>
	<div class="ChoiceGoods">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span>选择订单</span></header>
		<div class="container">
			<div>
				<span>选择</span>
				<span>商品代码</span>
				<span>数量</span>
				<span>已采集</span>
				<span>商品名称</span>
				<span>单位</span>
				<span>箱数</span>
			</div>
			<div v-for='(dataItem,index) in Consignment_ChoiceData'  :id="index"  :key='index'  @click="selectData(index)">
				<span class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></span>
				<span>{{dataItem.Sku}}</span>
				<span>{{dataItem.QtyTotal}}</span>
				<span>{{dataItem.QtyScan}}</span>
				<span>{{dataItem.Descr}}</span>
				<span>{{dataItem.Susr2}}</span>
				<span>{{dataItem.QtyCase}}</span>
			</div>
			<div class="kong"></div>
			<p class="positionFixed"><span class="g_singleBtn" @click="selectData_submit">确定</span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"ChoiceGoods",
		data(){
			return{

				Consignment_ChoiceData:[],//列表数据
				detail_item:0,//默认为第一个

			}
		},
		created(){

			console.log(this.$store.state.Consignment_ChoiceData)

			if(this.$store.state.Consignment_ChoiceData.length){
				var Consignment_ChoiceData = this.$store.state.Consignment_ChoiceData;

				this.Consignment_ChoiceData = Consignment_ChoiceData;
			}
		},
		methods:{
			// 返回上一页 页面
			goPrev:function(){

				var that = this;

				this.$router.push({
					name:"ConsignmentIndex",
					query:{
						InvoiceNum:that.$route.query.InvoiceNum,//发货单号
						CommodityCode:that.$route.query.CommodityCode,//商品代码
					}
				})
			},
			//点击选择具体订单
			selectData:function(idx){

				this.detail_item = idx;
			},
			// 点击确认按钮 跳转到录入单号页面
			selectData_submit:function(){

				this.$store.state.Consignment_SubmitData = [];

				this.$store.state.Consignment_SubmitData[0] = this.Consignment_ChoiceData[this.detail_item];

				this.$router.push({
					name:"ConsignmentNumCollect",
					query:{
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.ChoiceGoods{
		&>.container{
			width:100%;
			font-size: 30/50rem;
			padding-bottom: 130rpx;
			&>div{
				overflow: hidden;
				width:1300/50rem;
				height: 80/50rem;
				line-height: 80/50rem;
				border-bottom: 1/50rem solid #999;
				text-align: center;
				&>span{
					float: left;
					height:100%;
					width:200/50rem;
					text-align:center;
				}
				&>span:nth-child(1),&>span:nth-child(7){
					width:140/50rem;
				}
				.iconfont{
					font-size: 40/50rem;
				}
			}
			&>div.kong{
				height:200/50rem;
				border-bottom: none;
			}
			&>p.positionFixed{
				width: 100%;
				height:130/50rem;
				position: fixed;
				bottom: 0;

			}
		}
	}
</style>


