<template>
	<div class="ConsignmentNumCollect">
		<header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span>{{HeadTitle}}</span></header>
		<div class="container">
			<div class="fiexedTop">
				<div>
					<span>商品代码</span>
					<input  class="readOnly_input" readonly   v-model="input_CommodityCode"></input>
				</div>
				<div>
					<span>商品名称</span>
					<textarea  class="readOnly_input" v-model="input_CommodityName"   readonly></textarea>
				</div>
				<div>
					<span>箱号</span>
					<input  ref="input_BoxNum"   @keyup.enter="Submit_CollectCaseNo"   v-model="input_BoxNum"></input>
					<i v-if="input_BoxNum" @click="clearInput('input_BoxNum')"  class="iconfont icon-cuowu"></i>
				</div>

				<div>
					<span>序列号</span>
					<input  ref="input_SerialNum"  @keyup.enter="Submit_SerialNum"   v-model="input_SerialNum"  ></input>
					<i v-if="input_SerialNum" @click="clearInput('input_SerialNum')"  class="iconfont icon-cuowu"></i>
				</div>
			</div>
			<div class="table tableHead">
					<span>序列号</span>
					<span>箱号</span>
					<p>删除</p>
				</div>
			<div class="tableContainer">
				<div class="fixedHeight">
					<div class="table" v-for='(dataItem,index) in SerialTableArr'  :id="index"  :key='index' >
						<span>{{dataItem.SerailNo}}</span>
						<span>{{dataItem.CaseNo}}</span>
						<p @click="deleteSerial(index)">删除</p>
					</div>
				</div>
			</div>
		</div>
		<div class="bottomNum">
			<div>
				<span>{{shipmentsText}}</span>
				<input v-model="shipmentsNum"></input>
			</div>
			<div>
				<span>总采集</span>
				<input v-model="collectedTotal"></input>
			</div>
			<div>
				<span>当前采集</span>
				<input v-model="collectedNum"></input>
			</div>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @ref="inputFocus" @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"ConsignmentNumCollect",
		data(){
			return{
				input_CommodityCode:'',  //商品代码
				input_CommodityName:'',  //商品名称
				input_BoxNum:'',  //箱号
				input_SerialNum:'',  //序列号    S1806130001
				tipsHide:false,//提示信息是否出现
				msg:"",//提示信息内容
				SubmitData:[],//从上一个页面传来的数据
				SerialTableArr:[],//生成的序列号数组
				shipmentsNum:0,//发货数
				collectedNum:0,//当前采集
				collectedTotal:0,//总采集
				inputFocus:"",
				thisPage:"Receipt",
				HeadTitle:'收货序列号采集',//页面标题
				shipmentsText:"待收数",//或 发货数
			}
		},
		mounted() {

			this.$refs['input_BoxNum'].focus();
		},
		created(){

			var that = this;

			if(this.$store.state.IsConsignOrReceipt == "Receipt"){

				that.thisPage = "Receipt";
				that.HeadTitle = "收货序列号采集";
				that.shipmentsText = "待收数";

			}else if(this.$store.state.IsConsignOrReceipt == "Consignment"){

				that.thisPage = "Consignment";
				that.HeadTitle = "发货序列号采集";
				that.shipmentsText = "发货数";

			}

			if(that.$store.state.Consignment_SubmitData.length){

				var SubmitData = that.$store.state.Consignment_SubmitData;

				this.input_CommodityCode = SubmitData[0].Sku;//商品代码
				this.input_CommodityName = SubmitData[0].Descr;//商品名称

				this.SubmitData = SubmitData;

				this.shipmentsNum = SubmitData[0].QtyTotal;//发货数  或 待采集
				this.collectedTotal = SubmitData[0].QtyScan; //总采集
				
			}
		},
		methods:{
			// 返回上一页
			goPrev:function(){

				var that = this;

				this.$router.push({
					name:"ConsignmentIndex",
					query:{
						InvoiceNum:that.SubmitData[0].OrderKey,//发货单号
						CommodityCode:that.input_CommodityCode,//商品代码
					}
				})
			},
			// 根据 箱号 查询数据
			Submit_CollectCaseNo:function(){

				var that = this;

				if(this.thisPage == "Receipt"){

					var CaseNoUrl = this.HOST + "RFReceiptSerialCollectCaseNo.do";


				}else if(this.thisPage == "Consignment"){

					var CaseNoUrl = this.HOST + "RFSerialCollectCaseNo.do";

				}

				var CaseNoData = {
					"OrderKey":this.SubmitData[0].OrderKey,
					"CaseNo":this.input_BoxNum,
				}

				this.httpRequest(CaseNoUrl,CaseNoData,function(res){

					that.SerialTableArr = res.data.data;

					that.collectedNum = that.SerialTableArr.length;

					that.getFocus("input_SerialNum");

				},"input_BoxNum")
			},
			//根据 序列号 增加数据
			Submit_SerialNum:function(){

				var that = this;

				if(!this.input_SerialNum ){
					
					that.tipsShow("input_SerialNum");

					that.inputFocus = "input_SerialNum";

					that.msg = "序列号不能为空";

					return;
				}

				for(var i = 0; i < that.SerialTableArr.length ; i++){

					if(that.input_SerialNum == that.SerialTableArr[i].SerailNo){

						that.tipsShow("input_SerialNum");

						that.inputFocus = "input_SerialNum";

						that.msg = "序列号已存在";

						return;
					}
				}

				if(this.thisPage == "Receipt"){

					var SerialNumUrl = this.HOST + "RFReceiptSerialCollectSave.do";


				}else if(this.thisPage == "Consignment"){

					var SerialNumUrl = this.HOST + "RFSerialCollectSave.do";

				}

				var SerialNumData = {
					"OrderKey":this.SubmitData[0].OrderKey,//
					"CaseNo":this.input_BoxNum,
					"SerialNo":this.input_SerialNum,//用户输入序列号
					"Sku":this.SubmitData[0].Sku,//
					"StorerKey":this.SubmitData[0].StorerKey,//
				}

				this.httpRequest(SerialNumUrl,SerialNumData,function(res){

					var SerialTableArr = that.SerialTableArr;
					console.log(SerialTableArr.length)

					if(SerialTableArr.length == 0){
						console.log(SerialTableArr.length)

						SerialTableArr=[{
							CaseNo: that.input_BoxNum,
							OrderKey: that.SubmitData[0].OrderKey,
							SerailNo: that.input_SerialNum,
							Sku:that.SubmitData[0].Sku
						}]

					}else{

						SerialTableArr.unshift({
							CaseNo: that.input_BoxNum,
							OrderKey: that.SubmitData[0].OrderKey,
							SerailNo: that.input_SerialNum,
							Sku:that.SubmitData[0].Sku
						});

					}
					that.SerialTableArr = SerialTableArr;

					console.log(SerialTableArr.length)

					that.input_SerialNum = '';

					that.collectedNum = that.SerialTableArr.length;

					that.collectedTotal++;

				},"input_SerialNum")
			},
			// 删除 序列号采集
			deleteSerial:function(index){

				var that = this;

				if(this.thisPage == "Receipt"){

					var deleteSerialUrl = this.HOST + "RFReceiptSerialCollectDel.do";


				}else if(this.thisPage == "Consignment"){

					var deleteSerialUrl = this.HOST + "RFSerialCollectDel.do";

				}

				var deleteSerialData = {
					"OrderKey":this.SubmitData[0].OrderKey,//
					"CaseNo":this.input_BoxNum,
					"SerialNo":that.SerialTableArr[index].SerailNo,//序列号
				}

				this.httpRequest(deleteSerialUrl,deleteSerialData,function(res){

					that.SerialTableArr.splice(index,1);
					that.collectedNum = that.SerialTableArr.length;

					that.collectedTotal--;

				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.ConsignmentNumCollect{
		font-size: 29/50rem;
		.container{
			margin-bottom: 80/50rem;
			padding-top: 1/50rem;
			input,textarea{
				background-color:#fff;
				font-size: 29/50rem;
			}
			&>.fiexedTop{
				width: 100%;
				height: 320/50rem;
				position: fixed;
				z-index: 5;
				background-color: #fff;
				top:80/50rem;
				left: 0;
				&>div{
					position: relative;
					width:100%;
					box-sizing: border-box;
					margin-top: 20/50rem;
					overflow: hidden;
					span{
						width: 160/50rem;
						float: left;
						margin:0  20/50rem;
						text-align: right;
					}
					input{
						width:450/50rem;
						float: left;
						height:45/50rem;
						padding:0 10/50rem;
					}
					&>textarea{
						width:450/50rem;
						height:80/50rem;
						padding:0 10/50rem;
						float:left;
						outline:none;
					}
					i{
						position:absolute;
						right:90/50rem;
						font-size: 30/50rem;
						top: 6/50rem;
					}
				}
			}
			.tableContainer{
				width: 100%;
				margin-top: 358/50rem;
				padding-bottom: 50/50rem;
				.fixedHeight{
					margin:0 auto;
					width: 600/50rem;
				}
			}
			.table{
				width: 600/50rem;
				height: 50/50rem;
				line-height: 50/50rem;
				margin:0 auto;
				overflow: hidden;
				border-bottom: 1/50rem solid  #000;
				span{
					width: 300/50rem;
					height: 50/50rem;
					margin:0;
					padding:0;
					text-align: center;
					float: left;
					border-left: 1/50rem solid  #000;
				}
				span:nth-child(2){
					width: 198/50rem;
				}
				p{
					float: left;
					text-align: center;
					height: 50/50rem;
					width: 98/50rem;
					border-left: 1/50rem solid  #000;
					border-right: 1/50rem solid  #000;
				}
			}
			.tableHead{
				border-top: 1/50rem solid  #000;
				position: fixed;
				top: 400/50rem;
				left: 75/50rem;
				background-color: #fff;
				z-index: 6;
			}
			.readOnly_input{
				background-color: #f0f0f0;
				padding: 0 10/50rem;
			}
		}
		.bottomNum{
			width: 100%;
			height: 80/50rem;
			line-height: 80/50rem;
			position: fixed;
			left: 0;
			bottom: 0;
			background-color: #fff;
			z-index: 5;
			&>div{
				float: left;
				width: 240/50rem;
				height: 60/50rem;
				span{
					float:left;
					height: 60/50rem;
					margin-right:10/50rem;
				}
				input{
					width: 120/50rem;
					text-align: center;
					height: 40/50rem;
					font-size: 29/50rem;
					background-color:#f0f0f0;
				}
			}
			&>div:nth-child(3){
				width: 260/50rem;
			}
		}
	}
	@media screen and (max-width: 299px) and (min-width: 235px){
		.ConsignmentNumCollect{
			.bottomNum{
				&>div{
					font-size: 20/50rem;
					width: 240/50rem;
					span{
						width: 115/50rem;
						margin:0;
					}
					input{
						width: 120/50rem;
						padding:0;
						margin: 0;
					}
				}
				&>div:nth-child(3){
					width: 260/50rem;
					span{
						width: 150/50rem;
						margin:0;
					}
					input{
						width: 108/50rem;
					}
				}
				
			}
		}
	}
	@media screen and (max-width: 330px) and (min-width: 300px){
		.ConsignmentNumCollect{
			.bottomNum{
				&>div{
					font-size: 20/50rem;
					width: 250/50rem;
					span{
						width: 100/50rem;
						margin:0;
					}
					input{
						width: 130/50rem;
						padding:0;
						margin: 0;
					}
				}
				&>div:nth-child(3){
					span{
						width: 120/50rem;
						margin:0;
					}
					input{
						width: 125/50rem;
					}
				}
				
			}
		}
	}
</style>


