<template>

	<div class="CycleCount">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>盘点</span><i class="iconfont icon-saoma" @click="scan"></i></header>

		<div class="container">

			<div>
				<span>盘点单号</span>
				<input ref="CountNo" placeholder="请输入内容" v-model="CountNo" @keyup.enter="confirmOnclick"></input>
				<i v-if="CountNo" @click="clearInput('CountNo')" class="iconfont icon-cuowu"></i>
				<i class="iconfont icon-sousuo"  @click="choiceNo" ></i>
			</div>

			<div>
				<span>盘点类型</span>
				<select v-model="CountType" class="CountType" @change="CountTypeSelected">
					<option :value="type.id" v-for="type in CountTypeList">{{type.name}}</option>  
				</select>
			</div>

			<div>
				<span>盘点库位</span>
				<input ref="Loc" placeholder="请输入内容" v-model="Loc" @keyup.enter="confirmOnclick"></input>
				<i v-if="Loc" @click="clearInput('Loc')" class="iconfont icon-cuowu"></i>
			</div>

			<span @click="confirmOnclick" class="g_singleBtn">确认</span>
		</div>

		<div v-if="tipsHide" @touchmove.prevent class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p @ref="inputFocus" @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>

	</div>
</template>
<script type="text/javascript">

	export default{
		name:"CycleCount",
		data(){
			return{		
				CountNo:'', //测试数据 1234567
				CountTypeList:
				[{
					id: 'PSku',
					name: '商品'
				},
				{
					id: 'PSkuxLottable',
					name: '商品、批号'
				},
				{
					id: 'PSkuxLot',
					name: '商品、系统批号'
				}],
				CountType:'',
				Loc:'',  //6010120
				tipsHide:false, //提示信息是否显示
				msg:'',     //提示信息别人
				inputFocus:''
			}
		},
		mounted() {

			this.$refs.Loc.focus();
		},
		created(){
			this.CountType = this.CountTypeList[0].id;

			if(this.$route.query.SelectOrder){

				this.CountNo = this.$route.query.SelectOrder.CountNo;
				this.CountType = this.$route.query.SelectOrder.CountType;

			}
		},
		methods:{
			CountTypeSelected(){
				console.log(this.CountType);
			},
			// 跳转到 选择订单 页面
			choiceNo(){
				var that = this;

				var currentUrl = this.HOST + "RFCountSel.do";

				var postData = {
					"CountNo": this.CountNo,
				}

				this.httpRequest(currentUrl,postData,function(res){

					that.$store.state.CycleCount_RFCountSel = res.data.data;

					that.$router.push({
						name:"CycleCount_ChoiceNo",
						query:{

						}
					})
				},"CountNo")
			},
			confirmOnclick(){
				
				var that = this;

				if(!this.CountNo){

					this.msg = "盘点单号不能为空";
					that.tipsShow("CountNo");
					that.inputFocus = "CountNo";
				}else{
					if(!this.Loc){

						this.msg = "盘点库位不能为空";
						that.tipsShow("Loc");
						that.inputFocus = "Loc";
					}else{

						var currentUrl = this.HOST + "RFCountQry.do";

						var postData = {
							"CountNo":this.CountNo,
							"CountType":this.CountType,
							"Loc":this.Loc,
						}

						this.httpRequest(currentUrl,postData,function(res){

							that.$router.push({
								name:"CycleCount_Submit",
								query:{
									CountNo:that.CountNo,
									CountType:that.CountType,
									Loc:that.Loc
								}
							})
							that.$store.state.CycleCount_RFCountQry = res.data.data;
						},"CountNo")
					}
				}
			},
			//返回上一页  首页
			toIndex:function(){

				this.$router.push({
					name:"Index",
				})
			},
		}
	}
</script>
<style scoped lang="less">
.CycleCount{
	.container{
		font-size: 30/50rem;
		input{
			font-size: 25/50rem;
		}
		&>div{
			width: 100%;
			height: 80/50rem;
			margin-top: 20/50rem;
			padding: 25/50rem 20/50rem;
			box-sizing: border-box;
			position: relative;

			span{
				width: 140/50rem;
				text-align: right;
			}

			input::placeholder{
				font-size: 30/50rem;
			}

			input{
				margin-left: 10/50rem;
				width: 490/50rem;
				height: 55/50rem;
				border-radius: 5/50rem;
				border: 1/50rem solid #aaa;
				outline: none;
				padding: 0 10/50rem;
				font-size: 30/50rem;
			}
			i{
				position:absolute;
				right:90/50rem;
				font-size: 30/50rem;
				top: 36/50rem;
			}
			i.icon-sousuo{
				right:30/50rem;
				font-size: 40/50rem;
				top: 30/50rem;
			}
			.CountType{
				margin-left: 10/50rem;
				width: 512/50rem;
				height: 55/50rem;
				border-radius: 5/50rem;
				border: 1/50rem solid #aaa;
				outline: none;
				padding: 0 10/50rem;
				font-size: 30/50rem;
			}
		}
		
	}
}

@media screen and (width: 240px){
	.CycleCount{
		.container{
			font-size: 30/50rem;
			input{
				font-size: 25/50rem;
			}
			&>div{
				width: 100%;
				height: 80/50rem;
				margin-top: 20/50rem;
				padding: 25/50rem 20/50rem;
				box-sizing: border-box;
				position: relative;

				span{
					width: 140/50rem;
					text-align: right;
				}

				input::placeholder{
					font-size: 30/50rem;
				}

				input{
					margin-left: 10/50rem;
					width: 420/50rem;
					height: 55/50rem;
					border-radius: 5/50rem;
					border: 1/50rem solid #aaa;
					outline: none;
					padding: 0 10/50rem;
					font-size: 30/50rem;
				}
				i{
					position:absolute;
					right:140/50rem;
					font-size: 30/50rem;
					top: 36/50rem;
					color: #bbb;
				}
				i.icon-sousuo{
					right:35/50rem;
					font-size: 40/50rem;
					top: 35/50rem;
				}
				.CountType{
					margin-left: 10/50rem;
					width: 442/50rem;
					height: 55/50rem;
					border-radius: 5/50rem;
					border: 1/50rem solid #aaa;
					outline: none;
					padding: 0 10/50rem;
					font-size: 30/50rem;
				}
			}
		}
	}
}
</style>


